import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/suggestions",
    name: "Suggestions",
    component: function() {
      return import("../views/Suggestions.vue");
    },
  },
  {
    path: "/admin/edit-item/:cat_id/:item_id",
    name: "edit-item",
    component: function() {
      return import("../views/Edit-Item.vue");
    },
  },
  {
    path: "/admin/edit-cat/:cat_id/",
    name: "edit-cat",
    component: function() {
      return import("../views/Edit-cat.vue");
    },
  },
  {
    path: "/admin/add-item/:cat_id/",
    name: "add-item",
    component: function() {
      return import("../views/Add-item.vue");
    },
  },
  {
    path: "/admin/add-cat",
    name: "add-cat",
    component: function() {
      return import("../views/Add-cat.vue");
    },
  },
  {
    path: "/item/:cat_id/:item_id",
    name: "item",
    component: function() {
      return import("../views/Item.vue");
    },
  },
  {
    path: "/admin",
    name: "admin",
    component: function() {
      return import("../views/Admin.vue");
    },
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
